.Loader_loader__1v86V{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__SwUEx 1s infinite linear;
            animation: Loader_rotate__SwUEx 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__SwUEx {
    from{
        -webkit-transform:rotate(0deg) scale(1);
                transform:rotate(0deg) scale(1);
    }
    to{
        -webkit-transform:rotate(360deg) scale(1.4);
                transform:rotate(360deg) scale(1.4);
    }
    
}

@keyframes Loader_rotate__SwUEx {
    from{
        -webkit-transform:rotate(0deg) scale(1);
                transform:rotate(0deg) scale(1);
    }
    to{
        -webkit-transform:rotate(360deg) scale(1.4);
                transform:rotate(360deg) scale(1.4);
    }
    
}
